@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.nav-search-history {
  position: absolute;
  left: calc(50% - 19.2px);
  top: 30px;
  transform: translateX(-50%);
  background-color: #0d1017;
  width: 50%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  overflow-x: hidden;
  border: 0.1px solid rgb(201, 201, 201);
  border-top: none;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}
.searchHistory-container {
  display: flex;
  cursor: pointer;
  justify-content: space-between;
  padding: 8px 5px;
  border-bottom: 1px solid rgb(51, 51, 51);
}
.searchHistory-mainLink {
  display: block;
  width: 100%;
}
.searchHistory-container a {
  text-decoration: none;
  color: white;
  font-weight: 300;
}
.searchHistory-container:hover {
  background-color: #0366d6;
  transition: 100ms ease-in;
}
.searchHistory-user-container {
  display: flex;
  position: relative;
}
.searchHistory-userIcon {
  width: 20px;
  margin: 0 5px;
}
.searchHistory-jumpTo {
  position: absolute;
  display: none;
  background-color: #0d1017;
  color: rgb(75, 75, 75);
  border: 0.3px solid rgb(77, 77, 77);
  font-weight: 100;
  border-radius: 5px;
  padding: 3px 5px;
  font-size: 11px;
  right: 8px;
  z-index: 1;
}
.searchHistory-jumpTo-icon {
  width: 10px;
  height: 10px;
  margin-left: 5px;
  margin-top: 1px;
}
.searchHistory-container:hover .searchHistory-jumpTo {
  display: flex;
}

.toggle-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 60px;
  border-radius: 4px;
  padding: 2px;
  background-color: #0d1017;
}
.toggleBtn {
  width: 25px;
  z-index: 1;
  cursor: pointer;
}
.toggle-selected {
  position: absolute;
  background-color: #1f48ff;
  width: 25px;
  height: 25px;
  border-radius: 3px 0 0 3px;
  transition: 150ms ease-in-out;
}
.gridSelected {
  transform: translateX(122.5%);
  border-radius: 0 3px 3px 0;
  transition: 150ms ease-in-out;
}

.page404-goBackIcon {
  width: 20px;
  height: 20px;
  -webkit-animation: arrowAnim 800ms ease-in-out infinite;
          animation: arrowAnim 800ms ease-in-out infinite;
}
.page404-link {
  display: flex;
  justify-content: space-between;
  width: 100px;
  text-decoration: none;
  text-align: center;
  color: white;
  background-color: #1f48ff;
  padding: 6px 8px;
  border-radius: 5px;
}
.page404-link:hover {
  transition: 200ms ease-out;
  background-color: #153ceb;
}
.page404-link:active {
  background-color: #3f60f3;
}
@-webkit-keyframes arrowAnim {
  0%,
  100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-6px);
  }
}
@keyframes arrowAnim {
  0%,
  100% {
    transform: translateX(0);
  }

  50% {
    transform: translateX(-6px);
  }
}

.nav {
  width: 100%;
  position: fixed;
  background-color: #161b21;
  top: 0;
  padding: 5px 0;
  z-index: 10;
}
.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 0 auto;
}
.nav-homeLink {
  height: 50px;
}
.nav-githubIcon {
  cursor: pointer;
  width: 40px;
  margin: 5px 0;
}
.nav-searchIcon {
  width: 20px;
  height: 20px;
  margin-top: 4.5px;
  margin-left: 8.5px;
}
.nav-searchBtn {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  cursor: pointer;
  border: none;
  border: 0.4px solid rgb(156, 156, 156);
  outline: none;
  width: 40px;
  background-color: #0f1417;
}
.nav-searchBtn:hover {
  background-color: rgb(0, 53, 133);
  transition: 100ms ease-in;
}
.nav-searchBtn:active {
  background-color: rgb(1, 75, 185);
  transition: 100ms ease-in;
}
.nav-search-container {
  display: flex;
  justify-content: center;
  position: relative;
  flex: 0.9 1;
  width: 100%;
  height: 30px;
}
.nav-searchInput {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  border: 0.4px solid rgb(156, 156, 156);
  background-color: #0d1017;
  color: white;
  padding-left: 5px;
  outline: none;
  border-right: none;
  width: 50%;
}
.nav-searchInput:focus {
  border-color: #0366d6;
  transition: 100ms ease-in;
}

.remainingRequests-container {
  position: fixed;
  display: flex;
  flex-direction: column;
  right: 2%;
  top: 0;
  background-color: #0f1417;
  width: 100px;
  height: 80px;
  border: 1.5px solid rgb(1, 1, 20);
  border-top: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  transition: ease-out 140ms;
  z-index: 5;
}
.requests-container {
  cursor: pointer;
  background-color: #0f1417;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  border-top: 1px solid rgb(36, 36, 36);
}
.p-requests {
  color: white;
  text-align: center;
}
.requests {
  color: white;
  background-color: #0e1014;
  height: 100%;
}
.requestNumber {
  text-align: center;
  margin-top: 10px;
}
.remainingTime {
  text-align: center;
  padding-bottom: 5px;
  color: rgb(182, 181, 181);
  font-size: 12px;
}
.expanded {
  top: 60px;
  transition: ease-in-out 200ms;
}
.red {
  background-color: #c50000;
  border-radius: 0 0 4px 4px;
}
.userCard-container {
  display: inline-flex;
  flex-direction: column;
  background-color: #0d1017;
  width: 200px;
  border-radius: 4px;
  border-radius: 9px 9px 8px 8px;
}
.userCard-image {
  width: 100%;
  border-radius: 8px 8px 0 0;
  border: 0.4px solid rgb(211, 211, 211);
}
.userCard-login,
.userCard-repos {
  margin: auto 0;
  color: rgb(240, 240, 240);
  text-decoration: none;
}
.userCard-type-container {
  margin: 8px 0;
}
.userCard-repos-container {
  display: flex;
  flex: 0.95 1;
  align-items: center;
  font-size: 14px;
}
.userCard-info-container {
  display: flex;
  flex-direction: column;
  height: 170px;
  padding: 10px 2px;
  border: 0.4px solid rgb(211, 211, 211);
  border-top: none;
  border-radius: 0 0 8px 8px;
}
.userCard-repos-container,
.userCard-login-container,
.userCard-type-container {
  display: flex;
  justify-content: center;
}
.userCard-type {
  color: white;
  background-color: rgba(255, 0, 0, 0.8);
  border: 2px solid red;
  font-size: 12px;
  letter-spacing: 0.6px;
  border-radius: 50px;
  padding: 2px 10px;
}
.userCard-userIcon {
  width: 26px;
  margin-right: 4px;
}
.userCard-repoIcon {
  width: 30px;
  height: 30px;
}

.userRow-container {
  display: flex;
  border-radius: 4px;
  height: 145px;
  border: 0.4px solid rgb(211, 211, 211);
  background-color: #0d1017;
}
.userRow-info-container {
  display: flex;
  flex-direction: row;
  width: 100%;
  padding: 10px 5px;
}
.userRow-image {
  width: auto;
  height: 100%;
  border-right: 0.4px solid rgb(211, 211, 211);
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.userRow-login,
.userRow-repos {
  margin: auto 0;
  color: rgb(240, 240, 240);
  text-decoration: none;
}
.userRow-login-container {
  display: flex;
  flex-direction: column;
  align-self: center;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
  text-align: center;
  flex: 0.3 1;
}
.userRow-type-container {
  display: flex;
  flex: 0.25 1;
  justify-content: center;
  align-self: center;
  height: -webkit-min-content;
  height: -moz-min-content;
  height: min-content;
}
.userRow-type {
  color: white;
  background-color: rgba(255, 0, 0, 0.8);
  border: 2px solid red;
  font-size: 12px;
  letter-spacing: 0.6px;
  border-radius: 50px;
  padding: 2px 10px;
}
.userRow-repos-container {
  display: flex;
  flex: 0.45 1;
  align-items: center;
  font-size: 14px;
}
.userRow-repoIcon,
.userRow-userIcon {
  width: 35px;
}
.userRow-userIcon {
  margin: 0 auto;
}

@media (max-width: 550px) {
  .userRow-container {
    height: 120px;
  }
  .userRow-repoIcon,
  .userRow-userIcon {
    width: 25px;
  }
  .userRow-login,
  .userRow-repos {
    font-size: 14px;
  }
}
@media (max-width: 450px) {
  .userRow-container {
    height: 100px;
  }
  .userRow-login,
  .userRow-repos {
    font-size: 12px;
  }
  .userRow-type {
    font-size: 10px;
    padding: 2px 8px;
  }
}

.loader-container{
    width: 100vw;
    height: 80vh;
}
.loader{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}
.popularUsers-grid {
  display: flex;
  max-width: 65%;
  margin: 85px auto;
  flex-wrap: wrap;
  justify-content: space-around;
  grid-row-gap: 20px;
  row-gap: 20px;
}
.popularUsers-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 85px auto;
  max-width: 55%;
  grid-row-gap: 20px;
  row-gap: 20px;
}
.popularUsers-pageBtn-container {
  display: flex;
  width: 150px;
  margin: 40px auto;
}
.popularUsers-pageBtn {
  cursor: pointer;
  margin: 0 auto;
  width: 40px;
  border-radius: 5px;
  background-color: #165077;
}
.popularUsers-pageBtn:hover {
  background-color: #242e35;
  transition: 100ms ease-in;
}
.popularUsers-pageBtn:active {
  background-color: #011826;
}
.popularUsers-pageBtn-disabled {
  background-color: #566670;
  pointer-events: none;
}

@media (max-width: 1000px) {
  .popularUsers-grid,
  .popularUsers-list {
    max-width: 85%;
  }
}
@media (max-width: 500px) {
  .popularUsers-grid,
  .popularUsers-list {
    max-width: 95%;
  }
}
.container-404 {
  position: relative;
  margin-top: 60px;
  width: 100%;
  height: 80vh;
}
.title-container-404 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.page404-title,
.page404-subtitle {
  text-align: center;
  color: white;
}
.page404-title {
  font-size: 25vw;
}

.userPage {
  height: calc(100vh - 60px);
  margin-top: 60px;
  color: white;
}
.userPage-container {
  display: flex;
  margin: 0 auto;
  justify-content: space-between;
  width: 90%;
  padding-top: 40px;
}
.userPage-right-column {
  flex: 0.95 1;
  margin-top: 100px;
}
.userPage-name-container,
.userPage-repos-container {
  display: flex;
  align-items: center;
}
.userPage-type-container {
  display: inline-block;
}
.userPage-organization-link {
  text-decoration: none;
  color: white;
}
.userPage-type {
  color: white;
  background-color: rgba(255, 0, 0, 0.8);
  border: 2px solid red;
  font-size: 12px;
  letter-spacing: 0.6px;
  border-radius: 50px;
  padding: 2px 10px;
}
.userPage-avatar {
  width: 400px;
  border-radius: 50%;
  border: 4px solid #161b21;
}
.userPage-name-type-container {
  display: flex;
  align-items: center;
}
.userPage-name {
  color: white;
  text-decoration: none;
  margin-right: 30px;
}
.userPage-organization-avatar {
  width: 40px;
}
.userPage-reposIcon,
.userPage-userIcon {
  width: 40px;
}
.userPage-organizations-container {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 30px;
}
.organization-container {
  display: flex;
  flex-direction: column;
  background-color: #161b21;
  margin: 5px;
  padding: 5px;
  border-radius: 5px;
}
.userPage-organization-avatar {
  margin: 0 auto;
  padding-bottom: 5px;
}
.userPage-repos-container {
  margin: 20px 0;
}
.userPage-empty {
  color: #3d3d3d;
}

@media (max-width: 1070px) {
  .userPage-container {
    flex-direction: column;
  }
  .userPage-avatar {
    width: 400px;
  }
  .userPage-left-column {
    display: flex;
    justify-content: center;
  }
  .userPage-right-column {
    margin-top: 30px;
  }
  .userPage-type-container {
    margin-top: 20px;
  }
  .userPage-name-type-container {
    display: inline-block;
    width: 100%;
    margin: 0 auto;
    text-align: center;
  }
  .userPage-name-container,
  .userPage-repos-container {
    display: flex;
    justify-content: center;
  }
  .userPage-name {
    margin-right: 0;
  }
  .userPage-organizations-container {
    display: flex;
    justify-content: center;
  }
  .userPage-organizations-title {
    text-align: center;
  }
}
@media (max-width: 440px) {
  .userPage-avatar {
    width: 100%;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  font-family: "Roboto", sans-serif;
}
body {
  background-color: #060910;
  overflow-x: hidden;
}
.body {
  position: relative;
}

